import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent implements OnInit {
  title = "learningangular";
  color = "black";
  size = 16;
  colors = [];
  ngOnInit() {
    this.colors = [
      "red",
      "black",
      "thistle",
      "lavender",
      "plum",
      "violet",
      "mediumorchid",
      "darkorchid",
      "indigo",
      "purple",
      "maroon",
      "gray",
      "green",
      "blue"
    ];
  }
  outChangeSize(size) {
    this.size = size;
  }
  outReset() {
    this.color = "black";
    this.size = 16;
  }
  outGetChangeColor(color) {
    this.color = color;
  }
}
