import { Component, OnInit, EventEmitter, Input, Output } from "@angular/core";

@Component({
  selector: "app-listcolor",
  templateUrl: "./listcolor.component.html",
  styleUrls: ["./listcolor.component.css"]
})
export class ListcolorComponent implements OnInit {
  @Input("colors") colors;
  @Output("outChangColor") outChangColor = new EventEmitter();
  constructor() {}

  ngOnInit() {}
  onSetColor(color) {
    this.outChangColor.emit(color);
  }
}
